* {
  margin: 0;
  padding: 0;
}

.home {
  color: #EEEEEE;
  padding-left: 20px;
  width: 20%;
}

a:visited {
  color: #DBE2EF;
  
}

.headname {
  width: 55%;
}

.head {
  display: flex;
  align-items: center;
  background-color: #112D4E;
  color: #F9F7F7;

}

h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #112D4E;
  color: #F9F7F7;
}

form {
  padding-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #DBE2EF;
}

button {
  background-color: #3F72AF;
  color: #EEEEEE;
  margin : 3px;
  padding: 3px;
}

button:hover, button:active {
  background-color: #EEEEEE;
  color: #3F72AF;
  font-weight: bold;
}

.body {
  height: 300px;
  background-color: #DBE2EF;
}
